import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TableContainer,
  Paper,
  Button,
  TextField,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
import EditIcon from "@material-ui/icons/Edit";
import CheckIcon from "@material-ui/icons/Check";
import { CCol, CRow } from "@coreui/react";
import React, { useEffect, useState } from "react";
import { addObject, editObject } from "../../endpoint/object";
import { useSelector, useDispatch } from "react-redux";
function Donnees() {
  const devices = useSelector((state) => state.devices);
  const editMode = useSelector((state) => state.editMode);
  const dispatch = useDispatch();
  return (
    <div style={{ padding: "2% 18%" }}>
      <CRow>
        <CCol>
          <h1>Devices</h1>
        </CCol>
        <CCol style={{ textAlign: "end" }}>
          <Button onClick={() => addObject("/device")}>
            <AddIcon />
          </Button>
        </CCol>
      </CRow>
      <TableContainer component={Paper} style={{ padding: "10px" }}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Device ID</TableCell>
              <TableCell>Name</TableCell>
              <TableCell align="right">Actions</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {devices
              ? devices.map((device, index) => (
                  <TableRow key={device.id}>
                    <TableCell>{device.ID}</TableCell>
                    <TableCell>
                      {editMode && editMode.ID === device.ID ? (
                        <TextField
                          id="outlined-basic"
                          label="Description"
                          variant="outlined"
                          value={editMode.description}
                          onChange={(e) =>
                            dispatch({
                              type: "editMode",
                              editMode: {
                                ...editMode,
                                description: e.target.value,
                              },
                            })
                          }
                        />
                      ) : (
                        device.Description || ""
                      )}
                    </TableCell>
                    <TableCell align="right">
                      {editMode && editMode.ID === device.ID ? (
                        <Button
                          onClick={() => {
                            editObject("/device", {
                              Description: editMode.description,
                              ID: editMode.ID,
                            });
                            dispatch({ type: "editMode", editMode: null });
                          }}
                        >
                          <CheckIcon />
                        </Button>
                      ) : (
                        <Button
                          onClick={() =>
                            dispatch({
                              type: "editMode",
                              editMode: {
                                ID: device.ID,
                                description: device.Description,
                              },
                            })
                          }
                        >
                          <EditIcon />
                        </Button>
                      )}
                    </TableCell>
                  </TableRow>
                ))
              : null}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
export default Donnees;
